.content {
  margin: 0 auto;
  display: flex;
  justify-content: center;
}
.frame {
  order: 0;
  max-width: 377px;
  margin-right: 12px;
}
.circle {
  margin-top: 120px;
  display: grid;
  grid-template-columns: repeat(auto-fit, 80px);
  grid-template-rows: auto-fit;
  column-gap: 16px;
  justify-content: center;
  align-items: center;
}
